(function() {
    'use strict';

    angular
        .module('wadsworth')
        .run(runBlock);

    /** @ngInject */
    function runBlock($log, $rootScope, $state) {

        $log.debug('runBlock end');
        $rootScope.isState = function(state) {
            if ($state.current.name == state) {
                return true
            } else {
                return false
            }
        };
    }

})();

(function() {
    'use strict';

    angular
        .module('wadsworth')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider, $urlRouterProvider) {
        $stateProvider
            .state('home', {
                url: '/home',
                templateUrl: 'app/home/home.html',
                controller: 'HomeController',
                controllerAs: 'home'
            })
            .state('aboutus', {
                url: '/aboutus',
                templateUrl: 'app/aboutus/aboutus.html',
                controller: 'AboutusController',
                controllerAs: 'aboutus'
            })
            .state('careers', {
                url: '/careers',
                templateUrl: 'app/careers/careers.html',
                controller: 'CareersController',
                controllerAs: 'careers'
            })
            .state('contactus', {
                url: '/contactus',
                templateUrl: 'app/contactus/contactus.html',
                controller: 'ContactusController',
                controllerAs: 'contactus'
            })
            .state('experience', {
                url: '/experience',
                templateUrl: 'app/experience/experience.html',
                controller: 'ExperienceController',
                controllerAs: 'experience'
            })
            .state('news', {
                url: '/news',
                templateUrl: 'app/news/news.html',
                controller: 'NewsController',
                controllerAs: 'news'
            })
            .state('services', {
                url: '/services',
                templateUrl: 'app/services/services.html',
                controller: 'ServicesController',
                controllerAs: 'services'
            })

            .state('constellation', {
                url: '/constellation',
                templateUrl: 'app/constellation/constellation.html',
                controller: 'ConstellationController',
                controllerAs: 'constellation'
            })
            .state('constellation.conductor', {
                url: '/conductor/',
                controller: 'ConstellationConductorController',
                controllerAs: 'constellation_conductor',
                templateUrl: 'app/constellation/conductor/constellation-conductor.html'
            })
            .state('constellation.connections', {
                url: '/connections/',
                controller: 'ConstellationConnectionsController',
                controllerAs: 'constellation_connections',
                templateUrl: 'app/constellation/connections/constellation-connections.html'
            })
            .state('constellation.overview', {
                url: '/overview/',
                controller: 'ConstellationOverviewController',
                controllerAs: 'constellation_overview',
                templateUrl: 'app/constellation/overview/constellation-overview.html'
            })
            .state('constellation.vision', {
                url: '/vision/',
                controller: 'ConstellationVisionController',
                controllerAs: 'constellation_vision',
                templateUrl: 'app/constellation/vision/constellation-vision.html'
            });

        $urlRouterProvider.otherwise('/home');
    }

})();
